import React from "react";
import classes from "./Checkout.module.css";

const Checkout = (props) => {
  return (
    <form className={classes.form}>
      <section className={classes.names}>
        <div className={classes.control}>
          <label htmlFor="first-name">First Name</label>
          <input type="text" id="first-name" />
        </div>
        <div className={classes.control}>
          <label htmlFor="last-name">Last Name</label>
          <input type="text" id="last-name" />
        </div>
      </section>
      <section className={classes.street}>
        <div className={classes.control}>
          <label htmlFor="street-address">Street Address</label>
          <input type="text" id="street-address1" placeholder="House Number" />
          <input type="text" id="street-address2" placeholder="Street Name" />
        </div>
      </section>

      <section>
        <div className={classes.control}>
          <label htmlFor="city">Town/City</label>
          <input type="text" id="city" />
        </div>
        <div className={classes.control}>
          <label htmlFor="postal_code">Postal Code/Zip</label>
          <input type="number" id="postal_code" />
        </div>
      </section>
      <div className={classes.control}>
        <label htmlFor="phone_number">Phone Number</label>
        <input type="number" id="phone_number" />
      </div>
      <div className={classes.actions}>
        <button className={classes.close}>Close</button>
        <button className={classes.checkout}>Checkout</button>
      </div>
    </form>
  );
};

export default Checkout;
